import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import FilterTitle from "../FilterTitle";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type FilterTitleStoryProps = {
    mode: string,
    label: string,
    brand: BrandType
}

const FilterTitleStory = ({
    mode, brand, label
}: FilterTitleStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="FilterTitle">
                <Typography variant="h3">FilterTitle</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <FilterTitle label={label}/>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        label: "Label Text",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        label: { type: "string" }
    }
}

storiesOf("Components/Filtros/FilterTitle", module)
    .add("Default", (args) => <FilterTitleStory {...args}/>, argsControls);
